
*{
    margin: 0px;
    padding: 0px;

}
.page1{
    width: 90rem;
    height: 200px;
    margin: 5.175rem 0 6.356rem;
    padding: 6.5rem 15.438rem 6.313rem 13.813rem;
    display: grid;
    grid-template-columns: repeat(2 ,30%);
    justify-content: center;
    padding: 10px;
    gap: 10%;
}

@media (max-width:768px) {
    .page1{
        width: 90rem;
        height: 200px;
        margin: 5.175rem 0 6.356rem;
        padding: 6.5rem 15.438rem 6.313rem 13.813rem;
        display: grid;
        grid-template-columns: repeat(1 ,100%);
        justify-content: center;
        padding: 10px;
        gap: 10%;
        margin-bottom: 35px;
    }
    
}

.page1 , .left , .right , .starImage  {
    background-color: #5454d4;
}


.page1 p {
    background-color: #5454d4;
    color: white;
}

.starImage img{
    background-color: #5454d4;
}
.starImage{
    margin-bottom: 10px;
}





/* style for page 2 */

.container{
    display: grid;
    grid-template-columns: repeat(2 , 30%);
    justify-content: center;
    gap: 15%;
    
    
}

@media (max-width:768px) {
    .container{
        display: grid;
        grid-template-columns: repeat(1 , 100%);
        justify-content: center;
        gap: 15%;
        
        
    }
}
.page2Details {
    display: flex;
    flex-direction: column;
    gap: 15px;
}


.page2Right , #ima2{
    width: 200px;
}

@media(max-width:768px){
    .page2Details {
        display: flex;
        flex-direction: column;
       
    }

    .page2Right , #ima2{
        width: 170px;
        margin-left: 30%;
        margin-top: -60%;
    }
}

.page2RightImage , #img2{
    width: 250px;
    margin-top: 90%;
}

@media (max-width:768px){
    .page2RightImage , #img2{
        width: 250px;
       
    }
}
.points{
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 20px;
}



