* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    background-color: #fcfdfe;
    font-family: sans-serif;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px;
}

/* @media (max-width:768px) {
    
    .navbar {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 22px;
    
    } */

.logo{
    padding: 5px;
}

.menu {
    display: flex;
    gap: 10px;
}

.Topbtn {
    color: white;
    background-color: #c31a12;
    padding: 8px;
    border: none;
    border-radius: 6px;
    margin-top: -7px;
   
}

.details {
    display: grid;
    grid-template-columns: repeat(2, 30%);
    justify-content: center;
    padding: 6%;
}




@media (max-width: 768px) {
    .details {
        display: grid;
        grid-template-columns: repeat(1, 100%);
        justify-content: center;
        padding: 6%;
    }
    .img, #img{
        width: 100px;
        margin-left: 30%;
    }
    
}


.btn {
    background-color: #c31a12;
    padding: 8px;
    border: none;
    border-radius: 6px;
    padding: 10x;
    border-radius: 8px;
    color: white;

}

.text {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;

}

@media (max-width:768px){
    .text {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    
    }
}

.input {
    display: flex;
    margin-right: 50px;
    margin-top: 50px;
}

#input {
    height: 101%;
    width: 100%;
}


.points{
    margin-bottom: 80px;
}


.page2rightdetail{
    display: flex;
    flex-direction: column;
    margin-top: 55%;
    gap: 30px;
    margin-bottom: 80%;
}


.detail2{
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin-top: 55%;
}

@media (max-width:768px){
    .detail2{
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-top: 55%;
        padding: 40px;
        margin-top: 5%;
    }
}


